:root {
    /*========== Colors ==========*/
    --white-color: #FFFFFF;
    --grey-color: #F0F0F0;

    /** CSS DARK THEME PRIMARY COLORS */
    --color-primary-100: #f1d217;
    --color-primary-200: #f5d741;
    --color-primary-300: #f8dc5e;
    --color-primary-400: #fae177;
    --color-primary-500: #fde58e;
    --color-primary-600: #feeaa5;
    /** CSS DARK THEME SURFACE COLORS */
    --color-surface-100: #121212;
    --color-surface-200: #282828;
    --color-surface-300: #3f3f3f;
    --color-surface-400: #575757;
    --color-surface-500: #717171;
    --color-surface-600: #8b8b8b;
    /** CSS DARK THEME MIXED SURFACE COLORS */
    --color-surface-mixed-100: #796a22;
    --color-surface-mixed-200: #88793a;
    --color-surface-mixed-300: #978951;
    --color-surface-mixed-400: #a69969;
    --color-surface-mixed-500: #b5a981;
    --color-surface-mixed-600: #c4ba99;
  
    /*========== Font and typography ==========*/
    --body-font: "Pixelify Sans", sans-serif;
    --biggest-font-size: 2.5rem;
    --h1-font-size: 2rem;
    --h2-font-size: 1.25rem;
    --h3-font-size: 1.125rem;
    --normal-font-size: 1rem;
    --small-font-size: .813rem;
    --smaller-font-size: .75rem;
  
    /*========== Font weight ==========*/
    --font-regular: 400;
    --font-medium: 500;
    --font-semi-bold: 600;
    --font-bold: 700;
}

/*=============== BASE ===============*/
body {
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    height: 100vh;
    font-family: var(--body-font);
    color: var(--white-color)
}

button {
    font-family: var(--body-font);
}

ul, p{
    margin-block-start: 0px;
    margin-block-end: 0px;
    padding-inline-start: 0px;
}

li {
    list-style-type: none;
}

h1 {
    font-weight: lighter;
    font-size: var(--h1-font-size);
    margin-block-start: 0px;
    margin-block-end: 0px;
}

h2 {
    font-weight: lighter;
    font-size: var(--h2-font-size);
    margin-block-start: 0px;
    margin-block-end: 0px;
}

canvas {
    overflow: scroll;
}

/*=============== HEADER ===============*/
#headerContainer {
    display: flex;
    background-color: var(--color-surface-300);
    flex: 0 0 auto;
    width: 100%;
    align-items: center;
}

.title {
    font-size: var(--h2-font-size);
    padding-right: 10px;
    padding-left: 1px;
    padding-top: 1px;
    padding-bottom: 1px;
}

.settingsBar {
    display: flex;
    gap: 10px; /* Adds space between buttons */
}

.dropdown {
    position: relative;
    display: inline-block;
}

.dropbtn {
    background-color: var(--color-primary-400);
    color: var(--color-surface-100);
    font-size: var(--normal-font-size);
    border: none;
    display: flex;
    align-items: center;
    gap: 2px;
}

.settingButton {
    display: flex;
    align-items: center;
    gap: 4px;
}

.dropdown-content {
    display: none;
    position: absolute;
    background-color: #f1f1f1;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 3;
    font-size: var(--normal-font-size);
}

.dropdown-content div {
    color: black;
    padding: 4px;
    text-decoration: none;
}

.dropdown-content div:hover {
    background-color: #ddd;
}

.dropdown:hover .dropdown-content {
    cursor: pointer;
}

.dropdown:hover .dropbtn {
    background-color: var(--color-primary-100);
    cursor: pointer;
}



/*=============== MAIN ===============*/
.mainContainer{
    display: flex;
    flex: 1; /* Allow the container to grow and fill the available space */
}

/*=============== LEFT COLUMN ===============*/
.leftCol{
    background-color: var(--color-surface-300);
}

.toolBarList{
    width: 34px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.toolItem{
    background-color: var(--color-surface-mixed-200);
}

.toolButton{
    background-color: var(--color-primary-400);
    border: 1px solid var(--color-primary-400);
}

.toolButton:hover{
    background-color: var(--color-primary-100);
    border: 1px solid var(--color-primary-100);
    cursor: pointer;
}

.toolButton.selected {
    border: none;
    background-color:var(--color-primary-100);
}

/*=============== CENTER COLUMN ===============*/
.centerCol {
    display: flex;
    background-color: var(--color-surface-100);
    flex-grow: 1;
    flex-direction: column;
}

.canvasWrapper {
    position: relative;
    width: 640px;
    max-width: 1040px;
    height: 650px;
    max-height: 650px;
    overflow: auto;
    margin: auto;
}

#backgroundCanvas,
#pixelCanvas {
    position: absolute;
    image-rendering: pixelated;
}

#backgroundCanvas {
    z-index: 1;
}

#pixelCanvas {
    z-index: 2;
}

.canvasInfo {
    align-self: flex-end;
    margin-right: 20px;
    margin-bottom: 20px;
}


/*=============== RIGHT COLUMN ===============*/
.rightCol{
    background-color: var(--color-surface-300);
}

.colorPickerImg_Container{
    width: 200px;
    padding-left: 8px;
    padding-right: 10px;
}

.container{
    display: flex;
    flex-direction: column;
    gap: 5px;
}

.colorPrompt{
    margin-block-start: 2px;
    margin-block-end: 2px;
}

.hexCode{
    color: var(--second-color);
}

.palette-container{
    background-color: var(--color-surface-200);
    display: flex;
    flex-wrap: wrap;
    gap: 5px;
    height: 150px;
    overflow: scroll;
    align-content: flex-start;
}

#addColorSwatch{
    height: 20px;
}

.color-swatch{
    height: 20px;
    width: 20px;
}

.layersContainer{
    display: flex;
    height: 300px;
    background-color: var(--color-surface-200);
    flex-direction: column;
    overflow: scroll;
}

.layerInfo{
    display: flex;
    margin: 4px;
    background-color: var(--color-primary-400);
    gap: 10px;
}

.layerInfo:hover{
    background-color: var(--color-primary-100);
}

.layerName{
    margin-left: auto;
    color: var(--color-surface-100);
    width: 60px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

#add-layer{
    margin-left: 4px;
    margin-right: 4px;
}

.visibilityBtn{
    margin-left: 4px;
    width: 19px;
    height: 19px;
}

.imageElementContainer{
    display: flex;
    gap: 14px;
}

/*=============== MODAL ===============*/
.modal {
    display: none; /* Hidden by default */
    position: fixed; /* Stay in place */
    z-index: 4; /* Sit on top */
    left: 0;
    top: 0;
    width: 100%; /* Full width */
    height: 100%; /* Full height */
    overflow: auto; /* Enable scroll if needed */
    background-color: rgb(0,0,0); /* Fallback color */
    background-color: rgba(0,0,0,0.4); /* Black w/ opacity */
    padding-top: 60px;
}

.modal-content {
    color: #121212;
    background-color: #fefefe;
    margin: 5% auto; /* 15% from the top and centered */
    padding: 20px;
    border: 1px solid #888;
    width: 80%; /* Could be more or less, depending on screen size */
}

.close-button {
    color: #aaa;
    float: right;
    font-size: 28px;
    font-weight: bold;
}

.close-button:hover,
.close-button:focus {
    color: black;
    text-decoration: none;
    cursor: pointer;
}

.dimensions {
    padding-bottom: 10px;
}

.resizeWidthContainer{
    padding-bottom: 5px;
}

.resizeHeightContainer{
    padding-bottom: 12px;
}

.widthResize{
    padding-right: 6px;
}
