import { Layer } from "./layer";
import eye from '../../../assets/eye.png';
import eyeOff from '../../../assets/eye-off.png';
import mergeDownImg from '../../../assets/merge-down.png';
import deleteLayerImg from '../../../assets/delete-layer.png'
import draggableImg from '../../../assets/draggable.png';

export class LayersEventHandler{
    constructor(scaleManager, measurementsManager, bgManager, bgRenderer, colorManager, toolsManager){
        this.scaleManager = scaleManager;
        this.measurementsManager = measurementsManager;
        this.bgManager = bgManager;
        this.bgRenderer = bgRenderer;
        this.colorManager = colorManager;
        this.toolsManager = toolsManager;
        this.baseLayer = document.getElementById('backgroundCanvas');
        this.layers = [];
        this.zIndexCounter = 2;
        this.layerIndex = 0;
        this.currentLayerIndex = 0;

        // Reference to the container where all canvases are appended
        this.canvasContainer = this.baseLayer.parentElement;

        // Reference to the container where all the canvas info are appended
        this.layersInfoContainer = document.getElementsByClassName('layersContainer')[0];

        this.toggle = true;
        this.addLayerBtn = document.getElementById('add-layer');

        this.addLayer(); //Init default layer0 on program start

        this.init();
    }

    init(){
        this.addLayerBtn.addEventListener('click', () => this.addLayer());
    }

    getIndex(){

    }

    toggleVisibility(imgElement, layer){
        if (this.toggle){
            imgElement.src = eyeOff;
            layer.style.display = 'none';
            this.toggle = false;
        } else {
            imgElement.src = eye;
            this.toggle = true;
            layer.style.display = 'block';
        }
    }

    addLayer() {
        //NOTE: reccomended amount of layers is 9 or less, any more can cause performace issues

        //New layer creation
        const scale = this.scaleManager.getScale();
        const pixelWidth = this.measurementsManager.getPixelWidth();
        const pixelHeight = this.measurementsManager.getPixelHeight();
        const newLayer = new Layer(pixelWidth, pixelHeight, this.zIndexCounter, scale, this.layerIndex, this.bgManager, this.bgRenderer, this.colorManager, this.toolsManager, this.scaleManager, this.layers);
        const layerId = `pixelCanvas${this.layerIndex}`;
        const layerIdNum = this.layerIndex;
        const layerIndex = this.layerIndex;
        this.canvasContainer.appendChild(newLayer.canvas);

        //Create img element container
        const imgElementContainer = document.createElement('div');
        imgElementContainer.className = 'imageElementContainer';

        //Create layerInfo or newly created layer
        const layerInfo = document.createElement('div');
        layerInfo.className = 'layerInfo';
        layerInfo.id =`${layerId}layer`;
        layerInfo.addEventListener('click', () => this.selectLayer(layerIndex));

        // Create img element for visibility
        const imgElementVisibility = document.createElement('img');
        imgElementVisibility.src = eye;
        imgElementVisibility.className = 'visibilityBtn';
        imgElementVisibility.addEventListener('click', () => this.toggleVisibility(imgElementVisibility, newLayer.canvas));

        // Create p element for layer name
        const pElement = document.createElement('p');
        pElement.className = 'layerName';
        pElement.textContent = `Layer ${layerIdNum}`; // Set the text content of the p element

        //Create img element for merge-down
        const imgElementMergeDown = document.createElement('img');
        imgElementMergeDown.src = mergeDownImg;
        imgElementMergeDown.className = 'mergeDownBtn';
        imgElementMergeDown.addEventListener('click', () => this.mergeDownLayers());

        //Create img element for delete layer
        const imgElementDelete = document.createElement('img');
        imgElementDelete.src = deleteLayerImg;
        imgElementDelete.className = 'deleteLayerBtn';
        imgElementDelete.addEventListener('click', () => this.deleteLayer(layerIndex));

        //Create img element for dragging layer
        const imgElementDrag = document.createElement('img');
        imgElementDrag.src = draggableImg;
        imgElementDrag.className = 'dragLayerBtn';

        // Drag-and-drop event listeners
        imgElementDrag.addEventListener('dragstart', (event) => this.onDragStart(event, newLayer));
        imgElementDrag.addEventListener('dragover', (event) => this.onDragOver(event));
        imgElementDrag.addEventListener('drop', (event) => this.onDrop(event, layerIndex));

        // Append to containers
        layerInfo.appendChild(pElement);
        layerInfo.appendChild(imgElementContainer);
        imgElementContainer.appendChild(imgElementVisibility);
        imgElementContainer.appendChild(imgElementMergeDown);
        imgElementContainer.appendChild(imgElementDelete);
        imgElementContainer.appendChild(imgElementDrag);

        //Insert the layer info container before the add layer button
        this.layersInfoContainer.insertBefore(layerInfo, this.addLayerBtn);
        
        this.zIndexCounter += 1; // Increment zIndex for next layer
        this.layerIndex += 1; // Increment layerIndex for unique ID
    }

    selectLayer(layerIndex) {
        console.log(layerIndex)
        console.log('here')
        alert('here')
        if (layerIndex >= 0 && layerIndex < this.layers.length) {
            alert('bhere')
            // Deactivate event handlers for the current selected layer
            const currentLayer = this.layers[this.currentLayerIndex];
            currentLayer.middleColEventHandler.deinit(); // Implement a deinit() method to remove event listeners
            currentLayer.style.borderStyle = 'none';

            // Activate event handlers for the new layer to be selected
            const newLayer = this.layers[layerIndex];
            newLayer.middleColEventHandler.init();
            this.currentLayerIndex = layerIndex; //Update currentLayerIndex to the layerIndex passed in
            newLayer.style.borderStyle = 'solid pink'
        }
    }

    deleteLayer(layerIndex) {
        if (layerIndex > 0 && layerIndex < this.layers.length) {
            alert('delete layer')

            // Remove the layer's canvas element from the DOM
            const layerToRemove = this.layers[layerIndex];
            
            // Deinitialize event handlers for the layer to remove
            if (layerToRemove.middleColEventHandler) {
                layerToRemove.middleColEventHandler.deinit();
            }
            
            this.canvasContainer.removeChild(layerToRemove.canvas);
    
            // Remove the layer's info element from the DOM
            const layerInfoToRemove = document.getElementById(`pixelCanvas${layerIndex}layer`);
            this.layersInfoContainer.removeChild(layerInfoToRemove);
    
            //Switch layers to the layer beneath the layerToRemove
            this.switchLayer(layerIndex + 1);
            
            // Remove the layer from the layers array
            this.layers.splice(layerIndex, 1);
    
            // Adjust IDs and event listeners for layers and layer infos that come after the deleted layer
            for (let i = layerIndex; i < this.layers.length; i++) {
                this.layers[i].index -= 1;
                const layerInfo = document.getElementById(`pixelCanvas${i + 1}layer`);
                layerInfo.id = `pixelCanvas${i}layer`;
                layerInfo.querySelector('.layerName').textContent = `Layer ${i}`;
                layerInfo.removeEventListener('click', () => this.selectLayer(i + 1, layerInfo.id));
                layerInfo.addEventListener('click', () => this.selectLayer(i, layerInfo.id));
            }
    
            // Decrement layerIndex for new layers
            this.layerIndex -= 1;
        }
    }

    selectLayer(){

    }

    onDragStart(event, layer) {

        const layerCanvas = layer.canvas;
        const layerManager = layer.canvasManager; //undef bec init as const so out of scope
        const layerRenderer = layer.canvasRenderer;
        const layerMiddleColEventHandler = layer.middleColEventHandler;
        console.log({layerCanvas, layerManager, layerRenderer, layerMiddleColEventHandler})

        const layerIndex = this.layers.indexOf({layerCanvas, layerManager, layerRenderer, layerMiddleColEventHandler})
        console.log(layerIndex)
        event.dataTransfer.setData('text/plain', layerIndex);
    }
    
    onDragOver(event) {
        event.preventDefault();
    }
    
    onDrop(event, targetLayerIndex) {
        event.preventDefault();
        const draggedLayerIndex = parseInt(event.dataTransfer.getData('text/plain'), 10);
        if (draggedLayerIndex !== targetLayerIndex) {
            this.moveLayer(draggedLayerIndex, targetLayerIndex);
        }
    }
    
    moveLayer(fromIndex, toIndex) {
        // Update the layers array
        const [movedLayer] = this.layers.splice(fromIndex, 1);
        this.layers.splice(toIndex, 0, movedLayer);
    
        // Update the DOM elements
        const movedLayerInfo = document.getElementById(`pixelCanvas${fromIndex}layer`);
        const targetLayerInfo = document.getElementById(`pixelCanvas${toIndex}layer`);
        
        console.log(fromIndex, toIndex)

        if (fromIndex < toIndex) {
            // Dragging down: insert after the target layer
            this.layersInfoContainer.insertBefore(movedLayerInfo, targetLayerInfo.nextSibling);
        } else {
            // Dragging up: insert before the target layer
            this.layersInfoContainer.insertBefore(movedLayerInfo, targetLayerInfo);
        }

        console.log(this.layers)
    
        // Switch to the new layer
        //this.switchLayer(toIndex);
    }
    
    

    mergeDownLayers(){
        
    }

    mergeAllLayers(){

    }


}